'use client'

import { useState, useRef, useEffect } from 'react'
import Link from 'next/link'
import { useAIAssistant, avatarOptions as contextAvatarOptions, colorThemes as contextColorThemes, AIAssistantSettings, defaultAISettings } from '@/lib/contexts/AIAssistantContext'

// Predefined AI Avatar options with descriptions
const avatarOptions = [
  { id: 'robot', emoji: 'ü§ñ', name: 'Robot', description: 'Classic AI assistant' },
  { id: 'sparkles', emoji: '‚ú®', name: 'Sparkles', description: 'Magical helper' },
  { id: 'brain', emoji: 'üß†', name: 'Brain', description: 'Smart thinker' },
  { id: 'star', emoji: '‚≠ê', name: 'Star', description: 'Your shining assistant' },
  { id: 'rocket', emoji: 'üöÄ', name: 'Rocket', description: 'Fast & efficient' },
  { id: 'crystal', emoji: 'üîÆ', name: 'Crystal', description: 'Insightful advisor' },
  { id: 'lightning', emoji: '‚ö°', name: 'Lightning', description: 'Quick responder' },
  { id: 'heart', emoji: 'üíú', name: 'Heart', description: 'Caring helper' },
]

// Predefined color themes
